.TechCustomers__ {
  &container {
    display: flex;
    flex-direction: column;
    align-items: center;

    width: 100%;
    padding-top: 150px;
    padding-bottom: 150px;
    // height 100vh    100vh  ...
    //         div 100vh ...?
    //      ..

    // height: 100vh;

    background-color: $blue-white;

    .TechCustomers__title {
      font-size: 45px;
      font-weight: 700;
      padding-bottom: 30px;
      &_small {
        font-size: 14px;
        text-align: center;
        color: $blue_main;
        font-weight: 650;
        padding-bottom: 20px;
      }
    }

    .TechCustomers__map {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: center;

      padding-left: 50px;
      padding-right: 50px;
    }
  }
}

// 
@include responsive(T) {
}

@include responsive(M) {
  .TechCustomers__ {
    .TechCustomers__container {
      .TechCustomers__map {
        padding-left: 10px;
        padding-right: 10px;
      }
    }
  }
}
